@import '../../../../helpers/_variables';

.action-button {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  padding: 0 $padding;

  height: 27px;

  font-size: 11px;
  font-weight: 500;
  line-height: 11px;
  color: $grey;
  text-transform: uppercase;
  letter-spacing: 1px;

  outline: none;
  background: none;

  cursor: pointer;

  &__play {
    margin-right: $margin-sm;

    color: $white;

    background: $green;
    border: 1px solid $green;
    border-radius: 20px; }

  &__play-icon {
    display: flex;

    margin-right: $margin-xs; }

  &__follow {
    margin-right: $margin-sm;

    color: $sand;

    border: 1px solid $sand;
    border-radius: 20px; }

  &__more {
    text-align: center;

    width: 27px;
    height: 27px;
    padding: 0 0 0 4px;

    color: $sand;

    border: 1px solid $sand;
    border-radius: 50%; } }

